      }
    },

    handleAvatarSuccess(res) {
      //res这个参数自己打印出来一看便知，在此不做解释     
      console.log(res+1);
     //将后端发送的地址赋值到我们需要显示的img中的src动态绑定的参数中
      this.imageUrl = res.data.path;
     //将图片地址绑定到我们的form表单数据中 后期存入数据库中     
      this.tableData.photos_url = res.data.path;
    },
    beforeAvatarUpload(file) {
           const isJPG = file.type === "image/jpeg";
           const isPNG = file.type === "image/png";
           const isLt3M = file.size / 1024 / 1024 / 1024 < 3;
           if (!isJPG && !isPNG) {
             this.$message.error`在这里插入代码片`("上传头像图片只能是 JPG 格式或 png 格式!");
           }
           if (!isLt3M) {
             this.$message.error("上传头像图片大小不能超过 3MB!");
           }
           return isJPG && isLt3M;
         },

    mounted() {
        this.all()//每次进入该页面自动执行该方法，即自动读取数据库数据导入到页面当中
        var upload = document.getElementById("btnUpload");
        var avatar = document.getElementById("avatar");
        upload.onclick =function(){
          avatar.click(); //注意IE的兼容性
        };
    },

    methods: {
        all(){
       axios.get('http://localhost:3001/select_success').then(res=>{
         this.tableData=res.data;//数据传递到页面数组
         console.log("数据查询成功"+res.data)
         console.log(res.data)
         
       }).catch(err=>{
         console.log("获取数据失败"+err);
       })
     },
     Upload_file(data){       // data是上传的文件信息，里面有我们想要的file属性
      let reader = new FileReader() // 创建文件读取对象
      let file = data.file
      reader.readAsDataURL(file) // 文件读取转换为base64类型
      reader.onloadend = function (e) {
          console.log(e.target.result);
          alert("提交成功！")


          let params = {
            user: global.user,
            url: e.target.result,
            headers: {'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'},

          };
          // console.log(params)
          axios({
            method: "post",
            url: "http://localhost:3001/select_success/changeimg",
            data: params,//Qs.stringify({params})

          }).then(
            (response) => {
              // return response.data;
              console.log(response.data); 
            this.imageUrl = e.target.result;
            global.url=e.target.result
            alert("提交成功！")
            },
            (error) => {
              // console.log("错误", error.message);
              console.log(error); 
              alert("出问题啦！")
            }
          );

        }
      
      },
      Preview(ev){
        // const self=this;
        const file=ev.target.files[0];
        this.imgObj=ev.target.files[0];
        let obj=new FileReader();
        obj.readAsDataURL(file);
        // obj.onload=function(){
        //   self.img=obj.result;
        // }
        obj.onload=()=>{
            this.picReduce(obj.result,base64=>{
                this.img=base64
            });
        };
      },
    
      pic(){
        let _self=this;
        var fd = new FormData();
        let blob1 = this.dataURItoBlob(this.img);
        fd.append("physicalTestImg",blob1);
        axios.activity618SignUp(fd,function(msg){
            if(msg.success){
                _self.Toast(msg.alertMsg);
            }else{
                _self.Toast(msg.alertMsg);
                this.isSubmit=false;
            }
        })
      }
    },

//     mounted: function () {
//       var upload = document.getElementById("btnUpload");
//       var avatar = document.getElementById("avatar");
//       upload.onclick =function(){
//         avatar.click(); //注意IE的兼容性
//       };
//     },
    upload: function (e) {
        var that = this;
        let formData = new window.FormData();
        let file = e.target.files[0];
        formData.append('file',file);//通过append向form对象添加数据
        //利用split切割，拿到上传文件的格式
        var src = file.name,
          formart = src.split(".")[1];
        //使用if判断上传文件格式是否符合
        if (formart == "jpg" || formart == "png" ||
          formart == "docx" || formart == "txt" ||
          formart == "ppt" || formart == "xlsx" ||
          formart == "zip" || formart == "rar" ||
          formart == "doc") {
          //只有满足以上格式时，才会触发ajax请求
          this.$axios.post(this.$api.personalCenter.upFile,formData).then(function (res) {
            that.upFileData = res.data;
          }).then(function (res) {
            var params = {
              photos_url: that.upFileData,
              // photo_des: ''
            };
//            console.log(params.photos_url,'photos_url')
            that.$axios.post(that.$api.personalCenter.wallAdd,res.stringify(params)).then(function (res) {
              console.log(res.data);
              that.$options.methods.imgList.bind(that)();
            }).catch(function (err) {
              console.log(err);
              console.log("请求出错");
            })
          })
        } else {
          alert("文件格式不支持上传");
        }
      },
