    //       });

    //       this.tableData[0].imageUrl=e.target.result;
    //   }

    //   // this.$http.post(('http://localhost:3001/adminUser/imgUpload')+
    //   //     this.formValidate2.document_url
    //   // ,{emulateJSON:false}).then(response => { 
    //   //   console.log(response.body); 
    //   //   this.url = response.body; 
    //   //   alert("提交成功！")
    //   // }, response => { 
    //   //   console.log(response); 
    //   //   alert("出问题啦！")
    //   // });	
      
    // }
    
    return {
      ...toRefs(state),
