<template>
  <div class="login">
    <top-nav></top-nav>
    <br>
    <br>
    <a>用户名：</a>
    <input type="text" v-model="user" name="user">
    <br>
    <br>
    <a>密码：</a>
    <input type="text" v-model="password" name="password">
    <br><br>
    <button @click="login">登陆</button>
    <button @click="regis">注册</button>
    <button @click="del">删除账户</button>
  </div>
</template>

<script>
import topNav from '@/components/topNav.vue';
import axios from 'axios'
// import {  reactive, toRefs } from 'vue'
import global from "../global/global.js"
export default {
  components: { topNav },
  data() {
    return {
      tableData: [{
            // id:'',
            // date_time: '',
            // integral_number: '',
            // amount: '',
            user: '',
            password: '',
            photos_url: '',
            score: ''
        }],
        imageUrl: '',
        user: '',
        password: '',
        token: localStorage.getItem('token') // data里边定义，这是事先将token存在了Storage里边，现在可以直接获取
    };
  },
  mounted() {
        this.all()//每次进入该页面自动执行该方法，即自动读取数据库数据导入到页面当中
        
    },
  created() {},
  methods: {
      all(){
       axios.get('http://localhost:3001/select_success').then(res=>{
         this.tableData=res.data;//数据传递到页面数组
         console.log("数据查询成功"+res.data)
         console.log(res.data)
         
       }).catch(err=>{
         console.log("获取数据失败"+err);
       })
     },
    com() {
      window.location.href="static/index.html"; //跳转新页面
    },
    login(){
        console.log(this.user)
        var n;
        for(n=0;n<4;n++){
            if(this.tableData[n].user==this.user){
                if(this.tableData[n].password==this.password){
                    global.isLogin=true
                    global.user=this.tableData[n].user
                    console.log(global.isLogin)
                    this.$router.push('/other');
                }
            }
        }
        
    },
    regis(){
      var n;
      for(n=0;n<4;n++){
            if(this.tableData[n].user==this.user){
                alert("账号已存在")
            }
        }
        let params = {
            user: this.user,
            password: this.password,
            headers: {'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'},

          };
          // console.log(params)
          axios({
            method: "post",
            url: "http://localhost:3001/select_success/adduser",
            data: params,//Qs.stringify({params})

          }).then(
            (response) => {
              // return response.data;
              // console.log(response.config.data); 
              
              this.url = response.data; 

              alert("提交成功！")
              this.$router.push('/other');
              global.user=this.user
            },
            (error) => {
              // console.log("错误", error.message);
              console.log(error); 
              alert("出问题啦！")
            }
          );
    },
    del(){
      axios({
        method: 'delete',
        url: 'http://localhost:3001/select_success/deleteuser',
        // params: {}, // 请求参数拼接在url上
        data: {
          user: global.user,
          headers: {'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'},
        }  // 请求参数放在请求体
      }).then(res => {
        console.log(res)
      })
    }
  },

//   setup(){
//         const state = reactive({
//             pic: [{
//                 // id:'',
//                 // date_time: '',
//                 // integral_number: '',
//                 // amount: '',
//                 user: '',
//                 password: '',
//                 photos_url: '',
//                 score: ''
//             }]
//         })
//     //   function login(){
//     //     //   if()
//     //   }

//       return{
//           ...toRefs(state),
//         //   login
//       }
//   }
};
</script>


