  text-align: center;
  // background: rgb(229, 122, 161);
  padding-top: 8px;
  background: #b9b1aa;
  
  .topCenter{
    text-align: center;
    // width: 7rem;
    display: flex;
    justify-content: space-around;
    font-size: large;
    color: white;
    letter-spacing: 4px;
